body {
  background-color: #d9dbd7;
  padding: 40px;
}

.game-field {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

.start {
  background-color: rgba(0, 0, 0, 0);
  border-radius: 5px;
  margin-bottom: 20px;
  padding: 10px;
  font-size: 38px;
  transition: color .25s, background-color .25s;
}

.start:hover, .start:focus {
  color: #ff0;
  background-color: #00f;
  border-color: rgba(0, 0, 0, 0);
}

.container {
  display: flex;
}

.item {
  width: 100px;
  height: 100px;
  border: 2px solid #000;
  border-radius: 4px;
  justify-content: center;
  align-items: center;
  font-size: 40px;
  display: flex;
}

.item:not(:last-child) {
  margin-right: 10px;
}

.text {
  font-size: 40px;
}

